<template>
  <div id="taskAdd">
    <Modal
      v-model="taskBuild"
      title="新建核查任务"
      @on-cancel="reset"
    >
      <Form ref="taskAddForm" :model="taskAddData" :rules="taskAddRuleValidate" :label-width="85" style="padding:0 20%;">
        <FormItem prop="cl002" label="任务名称：">
          <Input v-model.trim="taskAddData.cl002" placeholder="请输入任务名称" size="default"/>
        </FormItem>
        <FormItem prop="cl016" label="行政区划：" >
          <Cascader :data="codeData" v-model="taskAddData.cl016" size="default" change-on-select></Cascader>
        </FormItem>
<!--        <FormItem prop="cl004" label="任务类型：">-->
<!--          <i-select v-model.trim="taskAddData.cl004" style="width: 100%" placeholder="请选择任务类型">-->
<!--            <i-option value="0">巡护</i-option>-->
<!--            <i-option value="1">核查</i-option>-->
<!--          </i-select>-->
<!--        </FormItem>-->
        <FormItem prop="cl010" label="任务年份：">
          <DatePicker size="default" :editable="false" type="year" v-model.trim="taskAddData.cl010" placement="bottom-end" placeholder="请选择任务年份" style="width: 100%"></DatePicker>
        </FormItem>
<!--        <FormItem prop="cl011" label="任务级别：">-->
<!--          <i-select v-model.trim="taskAddData.cl011" style="width: 100%" placeholder="请选择任务级别">-->
<!--            <i-option value="1">国家级</i-option>-->
<!--            <i-option value="2">市级</i-option>-->
<!--          </i-select>-->
<!--        </FormItem>-->
        <FormItem prop="ledgerIdList" label="任务台账：">
          <i-select v-model.trim="taskAddData.ledgerIdList" multiple filterable style="width: 100%" placeholder="请选择任务台账">
            <i-option v-for="item in ledgerList" :value="item.value">{{ item.label }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="cl009" label="任务描述：">
          <i-input v-model.trim="taskAddData.cl009" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入任务描述"></i-input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="taskAddSubmit">确定</Button>
        <Button @click="closeModal">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
// 格式化时间
import { renderYearTime } from '@/utils/common'
import {
  // 台账列表
  getLedgerList,
  // 新建任务
  insertTask
} from '@/api/taskManager'
export default {
  name: 'TaskAdd',
  data () {
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('任务台账不能为空'))
      } else {
        callback()
      }
    }
    return {
      /* 台账列表 */
      ledgerList: [],
      /* 添加数据表单参数 */
      taskAddData: {},
      /* 添加数据表单校验 */
      taskAddRuleValidate: {
        cl002: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        ledgerIdList: [
          { required: true, message: '任务台账必须选择', trigger: 'change', type: 'array' },
          { validator: validateCode, trigger: 'change' }
        ]
        // cl004: [
        //   { required: true, message: '任务类型必须选择', trigger: 'change', type: 'string' }
        // ]
      },
      /* 新建框 */
      taskBuild: false
    }
  },
  props: {
    /* 行政区划数据 */
    codeData: {}
  },
  mounted () {
    this.getLedgerList()
  },
  methods: {
    // 获取台账列表
    async getLedgerList () {
      await getLedgerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.ledgerList.push({ 'value': item.id, 'label': item.name })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 添加核查任务 */
    taskAddSubmit () {
      this.$refs.taskAddForm.validate(valid => {
        if (valid) {
          let code = ''
          if (this.taskAddData.cl016 === undefined || this.taskAddData.cl016.length === 0) {
            code = ''
          } else {
            code = this.taskAddData.cl016[this.taskAddData.cl016.length - 1]
          }
          insertTask({
            data: {
              cl002: this.taskAddData.cl002,
              // cl004: this.taskAddData.cl004,
              cl010: renderYearTime(this.taskAddData.cl010),
              ledgerIdList: this.taskAddData.ledgerIdList,
              // cl011: this.taskAddData.cl011,
              cl009: this.taskAddData.cl009,
              cl016: code
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '新建核查任务成功' })
              this.taskBuild = false
              this.taskAddData = {}
              this.reset()
              this.$emit('getTableData')
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.taskAddData = {
        cl002: '',
        // cl004: '',
        cl010: '',
        ledgerIdList: '',
        // cl011: '',
        cl009: '',
        cl016: []
      }
      this.$refs.taskAddForm.resetFields()
    },
    closeModal () {
      this.taskBuild = false
      this.reset()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
