<template>
  <div id="tablet">
    <div class="queryBox">
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
    </div>
    <div class="content_box">
      <div class="basicInfo">
        <p>
          <Icon type="ios-leaf" />
          台账基础信息
        </p>
        <ul>
          <li>
            <b>台账名称：</b>
            <span :title="tzName">
               {{ tzName }}
             </span>
          </li>
          <li>
            <b>台账备注：</b>
            <span :title="tzRemark">
               {{ tzRemark }}
             </span>
          </li>
          <li>
            <b>创建时间：</b>
            <span>
              {{ tzTime }}
             </span>
          </li>
         <!-- <li>
            <Button type="success" size="small" icon="ios-map" class="btn-tabBtn" @click.stop="goMap">地图查看</Button>
          </li>-->
        </ul>
      </div>
      <div>
        <Table border stripe :loading="spotsLoading" :columns="spotsColumns1" :data="spotsData1" highlight-row :max-height="tableHeight"></Table>
      </div>
    </div>
    <!-- 地图查看-->
    <div class="extract_box" :class="{spotsAddActive:spotsAddActive}">
      <taskManagementTableMap ref="taskManagementTableMap" @signOut="signOut"></taskManagementTableMap>
    </div>
  </div>
</template>

<script>
import taskManagementTableMap from './taskManagementTable_map'
import { renderDateTime } from '@/utils/common'
import {
  // 表格列表
  getTaskById
} from '@/api/taskManager'
import {
  // 斑块表格列表
  getPointFromStage
} from '@/api/taskScheduler'
export default {
  name: 'spotsTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor(window.innerHeight - 330),
      /* 表格数据 */
      spotsColumns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '斑块名称',
          align: 'center',
          minWidth: 200,
          key: 'activeName ',
          render: (h, params) => {
            return h('span', params.row.activeName)
          }
        },
        {
          title: '斑块编号',
          align: 'center',
          minWidth: 100,
          key: 'cd001 ',
          render: (h, params) => {
            return h('span', params.row.cd001)
          }
        },
        {
          title: '下发状态',
          align: 'center',
          minWidth: 100,
          key: 'sendStatus ',
          render: (h, params) => {
            if (params.row.activeName !== null && params.row.activeName !== '') {
              if (params.row.sendStatus === 1) {
                return h('span', '已下发')
              } else {
                return h('span', '未下发')
              }
            }
          }
        },
        /* {
          title: '下发人员',
          align: 'center',
          minWidth: 100,
          // key: 'sa019 ',
          render: (h, params) => {
            if (params.row.st4SysSa && params.row.st4SysSa.sa019) {
              return h('span', params.row.st4SysSa.sa019)
            } else {
              return h('span', '')
            }
          }
        } */
      ],
      spotsData1: [],
      /* 加载 */
      spotsLoading: false,
      /* 提取信息框状态 */
      spotsAddActive: false,
      tzTime: '',
      tzName: '',
      tzRemark: ''
    }
  },
  props: {
    /* 编辑行任务id */
    seeId: {}
  },
  components: {
    // 地图
    taskManagementTableMap
  },
  watch: {
    seeId () {
      this.dataId()
    }
  },
  mounted () {
    // 窗口改变时table自适应
    // window.addEventListener('resize', () => {
    //   this.tableHeight = Math.floor(window.innerHeight - 330)
    //   this.dataId()
    // }, false)
  },
  methods: {
    dataId () {
      this.getTableList(this.seeId)
      this.getBkList(this.seeId)
    },
    /* 获取台账基础字段列表 */
    getTableList (e) {
      getTaskById({
        data: {
          cl001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          if (res.data.data.data.rlhdGroupList.length > 0) {
            this.spotsData1 = res.data.data.data.rlhdGroupList
            this.tzTime = renderDateTime(this.spotsData1[0].createDate)
            this.tzName = this.spotsData1[0].name
            this.tzRemark = this.spotsData1[0].remark
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取台账基础字段列表 */
    getBkList (e) {
      this.spotsLoading = true
      getPointFromStage({
        data: {
          taskId: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          if (res.data.data !== '' && res.data.data !== null) {
            this.spotsData1 = res.data.data.st4ScsCdList
          } else {
            this.spotsData1 = []
            this.$Notice.warning({ title: '此任务下尚无绑定的问题斑块！' })
          }
          this.spotsLoading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
        this.spotsLoading = false
      })
    },
    /* 信息提取地图页面退出 */
    signOut (spotsAddActive) {
      this.spotsAddActive = spotsAddActive
    },
    // 查看地图
    goMap () {
      this.spotsAddActive = true
      this.$refs.taskManagementTableMap.initCom()
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    height: 100%;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
    }
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .content_box {
      margin: 20px 15px;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      bottom:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .spotsAddActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
    .basicInfo {
      margin-top: -10px;
      margin-bottom: 10px;
      p {
        text-align: left;
        font-size: 16px;
      }
      ul {
        overflow: hidden;
        line-height: 28px;
        padding: 0 20px;
        text-align: left;
        li {
          float: left;
          width: 20%;
          overflow: hidden;
          margin-right: 20px;
          b {
            float: left;
          }
          span {
            float: left;
            width: calc(100% - 69px);
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }
    }
  }
</style>
