<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox @searchSrldId="searchSrldId" style="width: calc(100% - 370px);float: left;"></QueryBox>
<!--      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: calc(100% - 340px)" ref="getQuery"></QueryBox>-->
      <div class="queryBtns">
<!--        <Button type="primary" size="small" icon="ios-paper-plane" class="btn-exportBtn" style="margin-right: 10px" @click.stop="handleIssue">下发</Button>-->
        <Button type="success" size="small" icon="md-add" class="btn-add" @click.stop="$refs.taskAdd.taskBuild = true">新建</Button>
        <Button type="primary" size="small" icon="md-cloud-download" @click="handleExport" class="exportBtn">批量导出</Button>
        <Button type="success" size="small" icon="md-cloud-download" style="margin-right: 10px">
          <a href="epr\epr\image\安徽核查任务导入模板.xls" style="color: #fff">下载模板</a>
        </Button>
        <Button type="success" size="small" class="btn-import" style="height: 24px;">
          <Upload
            class="upload-demo"
            action="/epr/api/task/importTask"
            name="file"
            :multiple="false"
            :on-success="uploadSuccess"
            :show-upload-list="false"
            accept=".xls"
            ref="upload">
            <Icon type="md-cloud-upload"></Icon>
            <span style="margin-left: 4px">批量导入</span>
          </Upload>
        </Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"
               ref="multipleTable"
               @on-selection-change="handleSelectionChange">
        </Table>
        <div class="page_box">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 新建-->
    <taskAdd ref="taskAdd" :codeData="codeData" @getTableData="getClearQueryData"></taskAdd>
    <!-- 修改-->
    <taskEdit ref="taskEdit" :codeData="codeData" :data="editDefaultData" @getTableData="getTableList"></taskEdit>
    <!-- 下发任务列表-->
    <IssueList ref="issueList" :taskIdArr="selectRow"></IssueList>
    <!-- 查看问题点-->
    <div class="spotsTable" :class="{addActive:addActive}">
      <spotsTable ref="spotsTable" :seeId="spotsSeeId" @signOut="signOut"></spotsTable>
    </div>
  </div>
</template>

<script>
import QueryBox from './areaQueryBox'
import taskAdd from './TaskAdd'
import taskEdit from './TaskEdit'
import IssueList from './IssueList'
import spotsTable from './spotsTable'
import { getMenu } from '@/api/acquisitionTable'
import {
  // 表格列表
  getTaskList,
  // 删除单条记录
  deleteTask,
  // 导出
  exportFile,
  // 导入
  importFile
} from '@/api/taskManager'
export default {
  name: 'extracTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格加载 */
      loading: false,
      /* 表格数据 */
      columns1: [
        // {
        //   type: 'selection',
        //   width: 60,
        //   fixed: 'left',
        //   align: 'center'
        // },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          // fixed: 'left',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          // fixed: 'left',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl002'
        },
        // {
        //   title: '任务类型',
        //   align: 'center',
        //   key: 'cl004',
        //   width: 100,
        //   render: (h, params) => {
        //     let data = params.row.cl004
        //     if (data === 0) {
        //       return h('span', {}, '巡护')
        //     } else if (data === 1) {
        //       return h('span', {}, '核查')
        //     } else if (data === null) {
        //       return h('span', {}, '')
        //     }
        //   }
        // },
        {
          title: '所属区划',
          align: 'center',
          width: 100,
          key: 'comName',
          render: (h, params) => {
            if (params.row.sysCompany && params.row.sysCompany.comName) {
              return h('span', {}, params.row.sysCompany.comName)
            } else {
              return h('span', {}, '')
            }
          }
        },
        {
          title: '任务年份',
          align: 'center',
          width: 100,
          key: 'cl010'
        },
        // {
        //   title: '任务级别',
        //   align: 'center',
        //   key: 'cl011',
        //   width: 100,
        //   render: (h, params) => {
        //     let data = params.row.cl011
        //     if (data === 1) {
        //       return h('span', {}, '国家级')
        //     } else if (data === 2) {
        //       return h('span', {}, '市级')
        //     } else if (data === null) {
        //       return h('span', {}, '')
        //     }
        //   }
        // },
        {
          title: '任务台账',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 200,
          render: (h, params) => {
            let row = params.row
            let optionArr = []
            if (row.rlhdGroupList.length > 0) {
              row.rlhdGroupList.forEach(item => {
                if (item.name) {
                  optionArr.push(item.name)
                }
              })
            }
            return h('div', {}, optionArr.join('，'))
          }
        },
        {
          title: '任务描述',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl009'
        },
        {
          title: '操作',
          key: '',
          width: 260,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              // 查看台账
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.spotsSeeId = params.row.cl001
                    this.addActive = true
                    this.$refs.spotsTable.getTableList(this.spotsSeeId)
                  }
                }
              }, '查看台账'),
              // 修改
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.taskEdit.taskEdit = true
                  }
                }
              }, '修改'),
              // 删除
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该核查任务吗？',
                      onOk: () => {
                        this.infoDel(params.row.cl001)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      /* 初始化页码 */
      pageNum: 1,
      /* 页容量自适应 */
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 查看问题点弹框状态 */
      addActive: false,
      /* 查看问题点的任务id */
      spotsSeeId: 0,
      // 多选相关参数
      selectRow: [],
      multipleSelection: [],
      /* 查询框返回查询条件 */
      obj: '',
      cl002: '', // 任务名称
      cl010: '', // 年份
      cl016: '', // 区划
      /* 行政区划数据 */
      codeData: []
    }
  },
  components: {
    QueryBox,
    // 新建任务弹窗
    taskAdd,
    // 编辑任务弹窗
    taskEdit,
    // 下发人员列表
    IssueList,
    // 查看问题点
    spotsTable
  },
  watch: {
    multipleSelection (data) { // 存储选中的row
      this.selectRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectRow.push(item.cl001)
        })
      }
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
    this.getCodeMenu()
  },
  methods: {
    /* 查询框 */
    searchSrldId (obj) {
      this.cl002 = obj.cl002
      this.cl010 = obj.cl010
      this.cl016 = obj.code
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getTaskList({
        data: {
          pageSize: this.pageSize,
          pageNumber: this.pageNum,
          cl002: this.cl002,
          cl010: this.cl010,
          cl016: this.cl016
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* axios: 删除数据 */
    infoDel (e) {
      deleteTask({
        data: {
          cl001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '删除成功' })
          // 避免刪除当前页最后一条数据时显示--无数据
          if (this.data1.length === 1 && this.pageNum > 1) {
            this.pageNum = this.pageNum - 1
          }
          this.getTableList()
        } else {
          this.$Message.error(res.data.msg)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 勾选checkbox事件
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    // 下发事件
    handleIssue () {
      this.$refs.issueList.issue = false
      if (this.selectRow.length < 1) {
        this.$Notice.error({ title: '请先勾选任务再点击下发！' })
      } else if (this.selectRow.length > 1) {
        this.$Notice.error({ title: '只能选择一个任务！' })
      } else {
        this.$refs.issueList.issue = true
      }
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.addActive = addActive
      if (addActive === false) {
        this.spotsSeeId = ''
      }
    },
    /* 新添加任务之后返回的任务列表同时清空查询条件 */
    getClearQueryData () {
      this.pageNum = 1
      this.getTableList()
    },
    // 上传成功钩子
    uploadSuccess (res, file) {
      if (res.code === '0000') {
        this.$Notice.success({ title: '批量导入核查任务成功！' })
        this.getTableList()
      } else {
        this.$Notice.error({ title: res.msg })
      }
    },
    /* 导出文件 */
    handleExport () {
      exportFile({
        data: {
          cl002: this.cl002,
          cl010: this.cl010,
          cl016: this.cl016
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          let data = res.data.data
          if (!data) {
            return
          }
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data.excelPath
          link.href = '/epr' + url
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 获取省市县菜单 */
    getCodeMenu () {
      getMenu({
        data: {
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.codeData = res.data.data
          this.codeData = this.valComCode(this.codeData)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 省市县菜单value = comCode */
    valComCode (arr) {
      for (let i in arr) {
        arr[i].value = arr[i].comCode
        this.valComCode(arr[i].children)
      }
      return arr
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .ml20 {
      margin-left: 20px;
    }
    .mr20 {
      margin-right: 20px;
    }
    .mt15 {
      margin-top: 15px;
    }
    .pull-right {
      float: right;
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px 0;
        text-align: right
      }
    }
    .spotsTable {
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      margin: 0 10px;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
  }
</style>
