<template>
  <div id="taskEdit">
    <Modal
      v-model="taskEdit"
      title="修改核查任务">
      <Form ref="taskEditForm" :model="taskEditData" :rules="taskEditRule" :label-width="85" style="padding:0 20%;">
        <FormItem prop="cl001" label="ID：" style="display: none">
          <Input v-model.trim="taskEditData.cl001"/>
        </FormItem>
        <FormItem prop="cl002" label="任务名称：">
          <Input v-model.trim="taskEditData.cl002 " placeholder="请输入任务名称" size="default"/>
        </FormItem>
        <FormItem prop="cl016" label="行政区划：">
          <Cascader :data="codeData" v-model="taskEditData.cl016" size="default" change-on-select></Cascader>
        </FormItem>
<!--        <FormItem prop="cl004" label="任务类型：">-->
<!--          <i-select v-model.trim="taskEditData.cl004" style="width: 100%" placeholder="请选择任务类型">-->
<!--            <i-option value="0">巡护</i-option>-->
<!--            <i-option value="1">核查</i-option>-->
<!--          </i-select>-->
<!--        </FormItem>-->
        <FormItem prop="cl010" label="任务年份：">
          <DatePicker size="default" type="year" :editable="false" v-model.trim="taskEditData.cl010" placement="bottom-end" placeholder="请选择任务年份" style="width: 100%"></DatePicker>
        </FormItem>
<!--        <FormItem prop="cl011" label="任务级别：">-->
<!--          <i-select v-model.trim="taskEditData.cl011" style="width: 100%" placeholder="请选择任务级别">-->
<!--            <i-option value="1">国家级</i-option>-->
<!--            <i-option value="2">市级</i-option>-->
<!--          </i-select>-->
<!--        </FormItem>-->
        <FormItem prop="ledgerIdList" label="任务台账：">
          <i-select v-model.trim="taskEditData.ledgerIdList" multiple filterable style="width: 100%" placeholder="请选择任务台账">
            <i-option v-for="item in editLedgerList" :value="item.value">{{ item.label }}</i-option>
          </i-select>
        </FormItem>
        <FormItem prop="cl009" label="任务描述：">
          <i-input v-model.trim="taskEditData.cl009" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入任务描述"></i-input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="taskEditSubmit">确定</Button>
        <Button @click="closeModal">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
// 格式化时间
import { renderYearTime } from '@/utils/common'
import {
  // 台账列表
  getLedgerList,
  // 修改任务
  updateTask
} from '@/api/taskManager'
export default {
  name: 'Taskedit',
  data () {
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('任务台账不能为空'))
      } else {
        callback()
      }
    }
    return {
      /* 数据表单校验 */
      taskEditRule: {
        cl002: [
          { required: true, message: '任务名称必须填写', trigger: 'blur' }
        ],
        ledgerIdList: [
          { required: true, message: '任务台账必须选择', trigger: 'change', type: 'array' },
          { validator: validateCode, trigger: 'change' }
        ]
        // cl004: [
        //   { required: true, message: '任务类型必须选择', trigger: 'change', type: 'string' }
        // ]
      },
      /* 编辑框 */
      taskEdit: false,
      // 编辑数据
      taskEditData: {},
      // 任务台账
      editLedgerList: []
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {},
    /* 行政区划数据 */
    codeData: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  created () {
    this.getLedgerList()
  },
  methods: {
    // 获取查询条件中台账列表
    async getLedgerList () {
      await getLedgerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.editLedgerList.push({ 'value': item.id, 'label': item.name })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.taskEditData.cl001 = this.data.cl001
      this.taskEditData.cl002 = this.data.cl002
      this.taskEditData.cl010 = this.data.cl010
      this.taskEditData.cl009 = this.data.cl009
      let row = this.data.rlhdGroupList
      let arr1 = []
      if (row) {
        if (row.length > 0) {
          row.forEach((item, index) => {
            arr1.push(item.id)
          })
        }
      }
      console.log(arr1)
      // $set避免select附不上值
      this.$set(this.taskEditData, 'ledgerIdList', arr1)
      // this.$set(this.taskEditData, 'cl004', String(this.data.cl004))
      // this.$set(this.taskEditData, 'cl011', String(this.data.cl011))
      let arr = []
      this.getDefaultCode(this.codeData, String(this.data.cl016), arr)
    },
    /* 设置编辑框行政区划默认值 */
    getDefaultCode (data, code, arr) {
      for (let i in data) {
        if (data[i].value === code) {
          arr.push(data[i].value)
          this.taskEditData.cl016 = JSON.parse(JSON.stringify(arr))
        } else {
          if (data[0].children.length > 0) {
            arr.push(data[i].value)
            this.getDefaultCode(data[i].children, code, arr)
          }
        }
      }
      arr.pop()
    },
    /* 编辑核查任务 */
    taskEditSubmit () {
      this.$refs.taskEditForm.validate(valid => {
        if (valid) {
          let code = ''
          if (this.taskEditData.cl016 === undefined || this.taskEditData.cl016.length === 0) {
            code = ''
          } else {
            code = this.taskEditData.cl016[this.taskEditData.cl016.length - 1]
          }
          updateTask({
            data: {
              cl001: this.taskEditData.cl001,
              cl002: this.taskEditData.cl002,
              cl010: renderYearTime(this.taskEditData.cl010),
              ledgerIdList: this.taskEditData.ledgerIdList,
              cl009: this.taskEditData.cl009,
              cl016: code
              // cl004: this.taskEditData.cl004,
              // cl011: this.taskEditData.cl011
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '修改核查任务成功' })
              this.$emit('getTableData')
              this.taskEdit = false
              this.taskEditData = {}
              this.reset()
            } else {
              this.$Message.error(res.data.msg)
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.taskEditData = {
        cl003: '',
        cl002: '',
        cl010: '',
        ledgerIdList: '',
        cl009: '',
        cl016: []
        // cl004: '',
        // cl011: ''
      }
      this.$refs.taskEditForm.resetFields()
    },
    closeModal () {
      this.taskEdit = false
     // this.reset()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
